import { useNavigate } from "react-router-dom"
import { Input, Button } from "components"
import { useState, useEffect } from "react"
import { ACLogo } from "assets/images"
import { LandingFormContainer, LandingForm, LandingFormLogoContainer, LandingFormTitle, LandingLink } from "components/common/landingRelatedPages.styled"
import { useAuth } from "contexts/AuthContext"
import Swal from "sweetalert2"

/**
 * [] 
 * @returns 
 */
const RegisterPage = () => {
    const [account, setAccount] = useState('')
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [checkPassword, setCheckPassword] = useState('')
    const [disabledSubmitBtn, setDisabledSubmitBtn] = useState(false)
    const { hasToken, currentRegistrant, register } = useAuth()
    let navigate = useNavigate()

    //  token
    //  -> ()  () 
    //  -> 
    useEffect(() => {
        if (hasToken) {
            if (currentRegistrant.role === 'admin') {
                navigate('/admin_main');
            } else {
                navigate('/main');
            }
        }
    }, [hasToken, navigate, currentRegistrant])

    // 
    async function handleSubmit(e) {
        e.preventDefault()
    }

    // 
    async function handleClick() {
        //  API  
        setDisabledSubmitBtn(true)

        // 
        if (account.length === 0 || name.length === 0 || email.length === 0 || password.length === 0 || checkPassword.length === 0) {
            setDisabledSubmitBtn(false)
            return;
        }

        //  API
        const response = await register({
            account,
            name,
            email,
            password,
            checkPassword
        })

        const isRegister = (response.status === 'success') ? true : false
        if (isRegister) {
            Swal.fire({
                title: '!',
                icon: 'success',
                showConfirmButton: false,
                timer: 3000,
                position: 'top',
            });
            navigate('/login');

        } else {
            setDisabledSubmitBtn(false)
            let errorMsgHtml = ''
            for (let error of response.errors) {
                errorMsgHtml += error + '<br/>'
            }
            
            Swal.fire({
                title: '!',
                icon: 'error',
                html: `<p>${errorMsgHtml}</p>`,
                showConfirmButton: false,
                timer: 3000,
                position: 'top',
            });
        }
    }

    return (
        <LandingFormContainer>
            <LandingForm onSubmit={handleSubmit}>
                <LandingFormLogoContainer>
                    <ACLogo />
                </LandingFormLogoContainer>

                <LandingFormTitle></LandingFormTitle>

                <Input
                    label=''
                    name='account'
                    placeholder=''
                    value={account}
                    required={true}
                    onChange={(e) => { setAccount(e.target.value) }}
                />
                <Input
                    label=''
                    name='name'
                    placeholder=''
                    value={name}
                    required={true}
                    maxLength={50}
                    onChange={(e) => { setName(e.target.value) }}
                />
                <Input
                    label='Email'
                    name='email'
                    type='email'
                    placeholder=' Email'
                    value={email}
                    required={true}
                    onChange={(e) => { setEmail(e.target.value) }}
                />
                <Input
                    label=''
                    name='password'
                    type='password'
                    placeholder=''
                    value={password}
                    required={true}
                    onChange={(e) => { setPassword(e.target.value) }}
                />
                <Input
                    label=''
                    name='passwordCheck'
                    type='password'
                    placeholder=''
                    value={checkPassword}
                    required={true}
                    onChange={(e) => { setCheckPassword(e.target.value) }}
                />

                <Button
                    type='submit'
                    size='large'
                    display='block'
                    text=''
                    onClick={handleClick}
                    disabled={disabledSubmitBtn}
                />
            </LandingForm>

            <LandingLink to="/login" className="block">
                
            </LandingLink>
        </LandingFormContainer>
    )
}

export default RegisterPage