import styled from "styled-components"
import { Input, Button } from "components"
import { useState, useEffect } from "react"
import { ACLogo } from "assets/images"
import { LandingFormContainer, LandingForm, LandingFormLogoContainer, LandingFormTitle, LandingLink } from "components/common/landingRelatedPages.styled"
import Swal from "sweetalert2"
import { useNavigate } from "react-router-dom"
import { useAuth } from "contexts/AuthContext"

const StyledLinksContainer = styled.div`
    text-align: right;
    vertical-align: middle;
`

/**
 * [] 
 * @returns 
 */
const LoginPage = () => {
    const [account, setAccount] = useState('')
    const [password, setPassword] = useState('')
    const [disabledSubmitBtn, setDisabledSubmitBtn] = useState(false)
    const { hasToken, userLogin, prevPath } = useAuth()
    let navigate = useNavigate()
    
    //  token
    useEffect(() => {
        if (hasToken) {
            navigate(prevPath);
        }
    }, [navigate, hasToken, prevPath]);
    
    // 
    function handleSubmit(e) {
        e.preventDefault()
    }

    // 
    async function handleClick () {
        //  API  
        setDisabledSubmitBtn(true)

        // 
        if (account.length === 0 || password.length === 0) {
            setDisabledSubmitBtn(false)
            return;
        }

        //  API
        const response = await userLogin({
            account,
            password
        })

        // 
        const isLogin = (response.status === 'success') ? true : false
        if (isLogin) {
            Swal.fire({
                title: '!',
                icon: 'success',
                showConfirmButton: false,
                timer: 3000,
                position: 'top',
            });
            
        } else {
            setDisabledSubmitBtn(false)
            Swal.fire({
                title: '!',
                icon: 'error',
                html: `<p>${response.message}</p>`,
                showConfirmButton: false,
                timer: 3000,
                position: 'top',
            });
        }
    }

    return (
        <LandingFormContainer>
            <LandingForm onSubmit={handleSubmit}>
                <LandingFormLogoContainer>
                    <ACLogo />
                </LandingFormLogoContainer>

                <LandingFormTitle> Alphitter</LandingFormTitle>

                <Input
                    label=''
                    name='account'
                    placeholder=''
                    value={account}
                    onChange={(e) => { setAccount(e.target.value) }}
                    required={true}
                />
                <Input
                    label=''
                    name='password'
                    type='password'
                    placeholder=''
                    value={password}
                    onChange={(e) => { setPassword(e.target.value) }}
                    required={true}
                />
                <Button
                    type='submit'
                    size='large'
                    display='block'
                    text=''
                    onClick={handleClick}
                    disabled={disabledSubmitBtn}
                />
            </LandingForm>

            <StyledLinksContainer>
                <LandingLink to="/register" title="" aria-label="">
                    
                </LandingLink>
                
                <LandingLink to="/admin" title="" aria-label="">
                    
                </LandingLink>
            </StyledLinksContainer>
        </LandingFormContainer>
    )
}

export default LoginPage