import styled from "styled-components"
import { TweetItem } from "."

const StyledTweetList = styled.div`
    width:100%;
    min-height: 600px;
`

/**
 * 
 * @param {array} tweetList - 
 * @param {function} onLikeToggle - /
 * @param {function} onShowReplyModal - 
 * @returns 
 */
const TweetList = ({tweetList, onLikeToggle, onShowReplyModal}) => {
    let TweetItems = <></>
    
    if (Array.isArray(tweetList)) {
        TweetItems = tweetList.map(tweet => {
            return(
                <TweetItem 
                    key={tweet.id}
                    item={tweet}
                    onLikeToggle={(id) => {
                        onLikeToggle?.(id)
                    }}
                    onShowReplyModal={(tweet) => {
                        onShowReplyModal?.(tweet)
                    }}
                />
            )
        })
    }

    return (
        <StyledTweetList>
            { TweetItems }
        </StyledTweetList>
    )
}
export default TweetList