import { useEffect } from "react"
import { Outlet, useNavigate } from "react-router-dom"
import { SideBar, SideBarList, SideBarItem } from "components"
import { useAuth } from "contexts/AuthContext"
import styled from "styled-components"

const StyledContainer = styled.div`
  width: 100%;
  max-width: 100%;
  @media screen and (min-width: 992px) {
    margin-left: auto;
    max-width: 960px;
  }
  @media screen and (min-width: 1200px) {
    max-width: calc(1140px + (50% - 570px));
  }
`

const StyledWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
`

const StyledStickyContainer = styled.div`
  position: -webkit-sticky;
  position: sticky;
  top: 16px;
`

const StyledSideBarContainer = styled.div`
  position: relative;
  width: 100%;
  max-width: 178px;
`

const StyledMain = styled.div`
  position: relative;
  border-left: 1px solid var(--gray-20);
  min-height: 110vh;
  width: 100%;
  max-width: calc(100% - 202px);
`

/**
 * []   Component
 * @returns 
 */
const AdminBasePage = () => {
  const { hasToken, currentRegistrant, logout } = useAuth()
  let navigate = useNavigate()

  //  token
  //  -> 
  //  -> ()  () 
  useEffect(() => {
    if (!hasToken) {
      navigate('/admin');
    } else if (hasToken && currentRegistrant.role !== 'admin') {
      navigate('/main');
    }
  }, [navigate, hasToken, currentRegistrant]);


  function handleLogOut() {
    logout()
  }

  // 
  if (!hasToken || (hasToken && currentRegistrant.role !== 'admin')) {
    return (
      <></>
    )
  }

  return (
    <StyledContainer>
      <StyledWrap>
        <StyledSideBarContainer>
          <StyledStickyContainer>
            <SideBar
              logOutButtonOnClick={handleLogOut}
            >
              <SideBarList>
                <SideBarItem to="/admin_main" text="" icon="home" />
                <SideBarItem to="/admin_users" text="" icon="user" />
              </SideBarList>
            </SideBar>
          </StyledStickyContainer>
        </StyledSideBarContainer>

        <StyledMain>
          <Outlet />
        </StyledMain>

      </StyledWrap>
    </StyledContainer>
  )
}

export default AdminBasePage