import styled from "styled-components"
import { Input, Button, Header } from "components"
import { useState } from "react"
import { editAccount} from "api/editAccount"
import { useAuth } from "contexts/AuthContext"
import Swal from "sweetalert2"


const SettingFormContainer = styled.div`
  width:100%;
  height: 100%;
`

const SettingForm = styled.div`
  width: 100%;
  position: relative;
  padding: 24px;
`

const StyledButtonWrapper = styled.div`
  position: absolute;
  right: 23px;
  Button { 
    cursor: pointer;
  }
`

/**
 * [] 
 * @returns 
 */
const SettingPage = () => {

    const [account, setAccount] = useState('')
    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [checkPassword, setCheckPassword] = useState('')
    const {currentRegistrant} = useAuth()

    
    // 
    function handleSubmit(e) {
    e.preventDefault()
    }
    
    // 
    async function handleSave () {

    //  editAccount API
    const response = await editAccount ({
      userId: currentRegistrant.id,
      account,
      name,
      email,
      password,
      checkPassword
      })
      
    // 
      const isSaved = (response.status === 'success') ? true : false
       if (isSaved) {
        Swal.fire({
                title: '!',
                icon: 'success',
                showConfirmButton: false,
                html: `<p>${response.message}</p>`,
                timer: 3000,
                position: 'top',
            });
       } else {
        //  
         let errorMsg = ''
         for (let error of response.errors) {
          errorMsg += error + '<br/>'
         }

        Swal.fire({
                title: '!',
                icon: 'error',
                showConfirmButton: false,
                html: `<p>${errorMsg}</p>`,
                timer: 3000,
                position: 'top',
            });
       }
      }

    return (        
            <SettingFormContainer>           
                <Header text=''/>
                <SettingForm onSubmit ={handleSubmit}>
                <Input
                    label=''
                    name='account'
                    placeholder=''
                    value={account}
                    required= {true}
                    onChange={(e) => { setAccount(e.target.value) }}
                />
                 <Input
                    label=''
                    name='name'
                    placeholder=''
                    value={name}
                    required={true}
                    maxLength={50}
                    onChange={(e) => { setName(e.target.value) }}
                />
                <Input
                    label='Email'
                    name='email'
                    type='email'
                    placeholder=' Email'
                    value={email}
                    required={true}
                    onChange={(e) => { setEmail(e.target.value) }}
                />
                <Input
                    label=''
                    name='password'
                    type='password'
                    placeholder=''
                    value={password}
                    required={true}
                    onChange={(e) => { setPassword(e.target.value) }}
                />
                <Input
                    label=''
                    name='passwordCheck'
                    type='password'
                    placeholder=''
                    value={checkPassword}
                    required={true}
                    onChange={(e) => { setCheckPassword(e.target.value) }}
                />
               <StyledButtonWrapper>
                <Button
                    type='submit'
                    text=''
                    size = 'large'
                    onClick = {handleSave}
                />
              </StyledButtonWrapper>
              </SettingForm>
            </SettingFormContainer>
    )
}

export default SettingPage
