import { createContext, useContext, useState, useEffect } from "react"
import { useLocation } from "react-router-dom"
import { userLogin, adminLogin, register } from "api/auth"
import jwt_decode from "jwt-decode";
import Swal from "sweetalert2";

const defaultAuthContext = {
  hasToken: false, //  token
  currentRegistrant: null, //  
  userLogin: null, // [] 
  adminLogin: null, // [] 
  register: null, // [] 
  logout: null, // 
  prevPath: null // 
}
const AuthContext = createContext(defaultAuthContext)

/**
 *  (AuthContext)
 * @returns 
 */
export const useAuth = () => useContext(AuthContext)

/**
 *  
 * @param {jsx} children -  useAuth  (AuthContext)  Component
 * @returns 
 */
export const AuthProvider = ({ children }) => {
  const [hasToken, setHasToken] = useState(false) //  token
  const [payload, setPayload] = useState(null) // token  payload 
  const [prevPath, setPrevPath] = useState('/main') // 
  const [logoutAlertMsg, setLogoutAlertMsg] = useState('')
  const { pathname } = useLocation()

  useEffect(() => {
    const token = localStorage.getItem('token')

    if (!token) {
      setHasToken(false)
      setPayload(null)

    } else {
      const tempPayload = jwt_decode(token)
      setHasToken(true)
      setPayload(tempPayload)
      
      // 
      const landingPaths = ['/admin', '/login', '/register', '/']
      if (!(landingPaths.includes(pathname))) {
        const adminPaths = ['/admin_main', '/admin_users']
        let prevPath = ''
        if (tempPayload.role === 'admin') {
          prevPath = adminPaths.includes(pathname) ? pathname : '/admin_main'
        } else {
          prevPath = !(adminPaths.includes(pathname)) ? pathname : '/main'
        }
        setPrevPath(prevPath)
      }
    }

  }, [pathname])

  // API  status  401, 403 
  useEffect(() => {
    if (logoutAlertMsg.length > 0) {
      Swal.fire({
        title: '!',
        icon: 'info',
        html: `<p>${logoutAlertMsg}</p>`,
        showConfirmButton: false,
        timer: 3000,
        position: 'top',
      });
    }
  }, [logoutAlertMsg])

  return (
    <AuthContext.Provider
      value={{
        //  token
        hasToken,
        //  
        currentRegistrant: payload && {
          ...payload
        },
        // [] 
        userLogin: async (data) => {
          const response = await userLogin({
            account: data.account,
            password: data.password
          })

          const isLogin = (response.status === 'success') ? true : false
          if (isLogin) {
            const token = response.data.token
            const tempPayload = jwt_decode(token)

            setPayload(tempPayload)
            setHasToken(true)
            setPrevPath('/main')
            localStorage.setItem('token', token)

          } else {
            setPayload(null)
            setHasToken(false)
            localStorage.removeItem('token')
          }
     
          console.log('[user login] :', response)
          return response
        },
        // [] 
        adminLogin: async (data) => {
          const response = await adminLogin({
            account: data.account,
            password: data.password
          })

          const isLogin = (response.status === 'success') ? true : false
          if (isLogin) {
            const token = response.data.token
            const tempPayload = jwt_decode(token)

            setPayload(tempPayload)
            setHasToken(true)
            setPrevPath('/admin_main')
            localStorage.setItem('token', token)

          } else {
            setPayload(null)
            setHasToken(false)
            localStorage.removeItem('token')
          }

          console.log('[admin login] :', response)
          return response
        },
        // [] 
        register: async (data) => {
          const response = await register({
            name: data.name,
            account: data.account,
            email: data.email,
            password: data.password,
            checkPassword: data.checkPassword
          })

          console.log('[register] :', response)
          return response
        },
        // 
        logout: (msg='') => {
          localStorage.removeItem('token')
          setPayload(null)
          setHasToken(false)
          setLogoutAlertMsg(msg)
        },
        // 
        prevPath
      }}
    >
      { children }
    </AuthContext.Provider>
  )
} 